<template><section><h1>树形选择框 TreeSelect</h1>
<hr>
<h2>基本用法</h2>
<div class="demo-block">
  <o-treeselect v-model="value" :multiple="true" :options="options"></o-treeselect>
</div>
</section></template>

<script>
export default {
  data () {
    return  {
      value: null,
      options: [
        {
          id: 'a',
          label: 'a',
          children: [{
            id: 'aa',
            label: 'aa',
            children: [
              {
                id: 'cc',
                label: 'cc'
              }
            ]
          }, {
            id: 'ab',
            label: 'ab',
          }],
        }, 
        {
          id: 'b',
          label: 'b',
        }, 
        {
          id: 'c',
          label: 'c',
        }
      ]
    }
  }
}
</script>


