<template><section>

<h1>图标 Icon</h1>
<hr>
<p>提供一套基于kendoUI常用的图标集合</p>
<h2>使用方式</h2>
<p>使用<code v-pre="" class="code_inline">OIcon</code>组件, 通过指定图标对应的type属性，示例代码</p>
<pre v-pre=""><code v-pre="">&lt;okendo-icon type=&quot;calendar&quot;&gt;&lt;/okendo-icon&gt;
</code></pre>
<p>渲染后为</p>
<pre v-pre=""><code v-pre="">&lt;i class=&quot;k-icon k-i-calendar&quot;&gt;&lt;/i&gt;
</code></pre>
<h2>属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>图标名称</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>图标大小, 单位是px</td>
<td>Number, String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>color</td>
<td>图标颜色</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>flip</td>
<td>图标倾斜角度</td>
<td>String</td>
<td>v, h</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>图标集合</h2>
<ul class="icon-list">
  <li v-for="name in icons" :key="name">
    <span>
      <o-icon :type="name"></o-icon>
      <span class="icon-name">{{'k-i-' + name}}</span>
    </span>
  </li>
</ul>
</section></template>
<style lang="less">
.container {
  ul.icon-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: solid 1px #eaeefb;
    border-radius: 4px;
  }

  .icon-list li {
    float: left;
    width: 16.66%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    color: #666;
    font-size: 13px;
    transition: color .15s linear;

    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;

    &::after {
      display: inline-block;
      content: "";
      height: 100%;
      vertical-align: middle
    }

    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      color: #99a9bf;
    }
    
    i {
      display: block;
      font-size: 32px;
      margin-bottom: 15px;
      color: #606266;
      width: auto;
      height: auto;
    }
    
    .icon-name {
      display: inline-block;
      padding: 0 3px;
      height: 1em;
      color: #606266;
    }
    
    &:hover {
      color: rgb(92, 182, 255);
    }
  }
}
</style>
<script>
  var iconList = require('../icon.json')

  export default {
    data () {
      return {
        icons: iconList
      }
    }
  }
</script>


