<h1>输入框 Input</h1>
<hr>
<h2>基础用法</h2>
<p>通过鼠标键盘输入字符</p>
<div class="demo-block">
  <okendo-input v-model="value" placeholder="请输入内容"></okendo-input>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><okendo-input v-model="value" placeholder="请输入内容"></okendo-input>

</body></html></div>
