<h2>可清空</h2>
<div class="demo-block">
  <okendo-input v-model="value2" placeholder="请输入内容" clearable=""></okendo-input>
</div>

<p>使用<code v-pre="" class="code_inline">clearable</code>属性即可得到一个可清空的输入框</p>
