<h2>带Icon的输入框</h2>
<p>可以通过<code v-pre="" class="code_inline">prefix-icon</code>和<code v-pre="" class="code_inline">suffix-icon</code>属性在input组件的首部或者尾部增加显示图标，也可以通过<code v-pre="" class="code_inline">slot</code>来放置图标</p>
<div class="demo-block">
  <okendo-input placeholder="请输入内容" v-model="value3" prefix-icon="k-i-search">
  </okendo-input>
  <okendo-input placeholder="请输入内容" v-model="value4" suffix-icon="k-i-calculator">
  </okendo-input>
  <p>slot 方式</p>
  <okendo-input placeholder="请输入内容" v-model="value5">
    <i slot="prefix" class="okendo-input__icon k-icon k-i-search"></i>
  </okendo-input>
  <okendo-input placeholder="请输入内容" v-model="value6">
    <i slot="suffix" class="okendo-input__icon k-icon k-i-calculator"></i>
  </okendo-input>
</div>

